<template>
	<div class="table-content homepage_table_content">
		<div class="table-content-inner">
			<div class="homepage_logo_box flex_center">
				<div v-if="company.logo" class="homepage_logo_left">
					<img :src="company.logo" @error="getImgurl" alt="">
				</div>
				<div class="homepage_logo_right">
					<h2>{{company.mechanism_name}}</h2>
					<h3>XPE 智能办公平台</h3>
				</div>
			</div>
			<div class="download-box">
				<div class="download-content">
					<div class="download-text">
						<h2>客户端下载</h2>
						<h3>打开手机扫描二维码，即可体验移动办公带来的便捷</h3>
						<h4>Copyright © Reserved 搜股(上海)科技有限公司</h4>
					</div>
					<div class="download-content-right">
						<div class="download-imgbox">
							<img :src="androidInfo.path">
							<div class="download-btn-info-line">
								<div class="download-btn-info-line1">
									<img src="../../images/homepage_download_android.png" alt=""> XPE 安卓版
								</div>
								<div class="download-btn-info-line2">
									<span>版本{{androidInfo.version}}</span>
									<span>{{androidInfo.add_time}}</span>
								</div>
							</div>
						</div>
						<div class="download-imgbox">
							<img :src="iosInfo.path">
							<div class="download-btn-info-line">
								<div class="download-btn-info-line1">
									<img src="../../images/homepage_download_ios.png" alt=""> XPE iOS版
								</div>
								<div class="download-btn-info-line2">
									<span>版本{{iosInfo.version}}</span>
									<span>{{iosInfo.add_time}}</span>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<el-dialog title="" :visible.sync="dialogVisible" width="580px" :modal="false" class="updateinfo_modal">
			<div class="updateinfo_modal_body">
				<span class="iconfont icon-guanbi" @click="dialogVisible=false"></span>
				<div class="updateinfo_modal_title">{{versioninfo.version}}版本更新说明</div>
				<div class="updateinfo_modal_time">更新时间：{{versioninfo.add_time}}</div>
				<div class="updateinfo_modal_textbox" v-html="versioninfo.info">
				</div>
				<div class="updateinfo_modal_btn" @click="dialogVisible=false">我知道了</div>
			</div>
		</el-dialog>
	</div>
</template>
<script>
	import { latestApp, getVersion } from '../../config/api'
	import {
		setStore,
		getStore,
		removeStore,
		dialogConfirm
	} from '../../config/mUtils'
	export default {
		data() {
			return {
				androidInfo: {},
				iosInfo: {},
				company: {},
				//更新弹窗
				dialogVisible: false,
				versioninfo: '',
				info: ''
			}
		},
		created() {
			latestApp().then(res => {
				if(res.data.msgNo == 1) {
					this.androidInfo = res.data.payload.android;
					this.iosInfo = res.data.payload.ios;
					this.company = res.data.payload.company;
					this.androidInfo.add_time = this.androidInfo.add_time.split(" ")[0];
					this.iosInfo.add_time = this.iosInfo.add_time.split(" ")[0];
				}
			})
			this.checkVersion();
		},
		components: {

		},
		computed: {},
		mounted() {},
		methods: {
			checkVersion() {
				// let version = '';
				// let localversion = '';
				// if (!getStore('htcx_version')) {} else {
				//   localversion = getStore('htcx_version');
				// }
				let userinfo = JSON.parse(getStore('htcx_userInfo'))
				if(userinfo.version !== 1) {
					return;
				}
				getVersion({}).then(res => {
//					this.dialogVisible = true;
					userinfo.version = 0;
					setStore('htcx_userInfo', userinfo);
					let str = res.data.payload.info;
					this.versioninfo = res.data.payload;
					let regs = new RegExp("\r", "g");
					let reg = new RegExp("\n", "g");
					let regSpace = new RegExp(" ", "g");
					str = str.replace(reg, "<br/>");
					str = str.replace(regSpace, "&nbsp;");
					this.versioninfo.info = str;
				})
			},
			getImgurl(e) {
				e.currentTarget.src = '../../static/images/company_headerimg_normal.png';
				e.onerror = null;
			},
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin.scss';
	.download-imgbox {
		padding: 8px;
		margin-right: 40px;
		display: flex;
		flex-shrink: 0;
		&>img {
			width: 100px;
			height: 100px;
			margin-right: 10px;
		}
		.download-btn-info-line1 {
			font-size: 14px;
			margin-bottom: 2px;
			margin-top: 2px;
			font-weight: bold;
			color: #35383A;
			display: flex;
			align-items: center; //justify-content:center;
			img {
				width: 16px;
				height: 16px;
				margin-right: 8px;
			}
		}
		.download-btn-info-line2 {
			//text-align: center;
			span {
				font-size: 13px;
				color: #A0A4AA;
				margin-right: 10px;
				line-height: 1em;
			}
		}
	}
	
	.download-box {
		margin-top: 15px;
		overflow: hidden;
		.download-bg img {
			width: 100%;
			min-height: 250px;
		}
	}
	
	.download-content {
		display: flex;
		align-items: center;
		justify-content: space-between;
		box-sizing: border-box;
		.download-content-right {
			display: flex;
			align-items: center;
		}
		.download-text {
			//margin-right: 247px;
			h2 {
				font-size: 28px;
				font-weight: bold;
				color: #35383A;
				margin-bottom: 18px;
				line-height: 1em;
			}
			h3 {
				font-size: 18px;
				font-weight: normal;
				color: #35383A;
				line-height: 1em;
				margin-bottom: 16px;
			}
			h4 {
				font-size: 12px;
				font-weight: normal;
				color: #A2A2A2;
				line-height: 1em;
			}
		}
		.download-btn {
			width: 280px;
			height: 80px;
			border: 1px solid #fff;
			background: none;
			transition: all 0.3s;
			margin-right: 30px;
			position: relative;
			box-sizing: border-box;
			&:hover {
				background: rgba(255, 255, 255, 0.26);
				.download-btn-encode {
					visibility: visible;
					opacity: 1;
					top: -305px;
				}
			}
		}
		.download-btn-info {
			padding-left: 25px;
			padding-top: 12px;
			box-sizing: border-box;
			.download-btn-info-line1 {
				line-height: 30px;
				font-size: 22px;
				margin-bottom: 8px;
				color: #FFFFFF;
				img {
					width: 30px;
					height: 30px;
					float: left;
					margin-right: 14px;
				}
			}
			.download-btn-info-line2 {
				span {
					font-size: 14px;
					color: #C2E5FF;
					margin-right: 10px;
					line-height: 1em;
				}
			}
		}
		.download-btn-encode {
			visibility: hidden;
			width: 280px;
			height: 280px;
			background: #fff;
			opacity: 0;
			position: absolute;
			top: -315px;
			left: 0px;
			border-radius: 8px;
			padding: 10px;
			box-sizing: border-box;
			box-shadow: 0px 0px 14px 0px rgba(195, 195, 195, 1);
			transition: all 0.3s;
			img {
				width: 100%;
				height: 100%;
			}
			&::before {
				position: absolute;
				content: '';
				display: block;
				width: 0;
				height: 0;
				bottom: -13px;
				@include cl;
				border-left: 18px solid transparent;
				border-right: 18px solid transparent;
				border-top: 13px solid #fff;
			}
		}
	}
	
	.l-column-left {
		margin-left: 62px;
	}
	
	.table-content {
		min-width: 100px;
		min-height: 100px;
		overflow: auto;
		width: auto;
		padding: 0px;
	}
	
	.table-content-inner {
		height: 100%;
		width: 100%;
		min-height: 640px;

		position: relative;
		background-size: 100% 100%;
		background-attachment: fixed;
		background-repeat: no-repeat;
	}
	
	.homepage_logo_box {
		height: 640px;
		background: url('../../images/homepage_bg.png');
		background-size: 100% 100%;
		background-repeat: no-repeat;
		.homepage_logo_left {
			width: 80px;
			height: 80px;
			margin-right: 20px;
			img {
				width: 100%;
				height: 100%;
				border: 3px solid #fff;
				border-radius: 50%;
			}
		}
		.homepage_logo_right {
			h2 {
				color: #5290FF;
				font-size: 28px;
				font-weight: bold;
				margin-bottom: 10px;
				line-height: 1em;
			}
			h3 {
				color: #5290FF;
				font-size: 15px;
				line-height: 1em;
			}
		}
	}
	
	//更新弹窗
	.updateinfo_modal_body {
		height: 510px;
		overflow: hidden;
		position: relative;
		background: url('../../images/updateinfo_modal_bg.png') no-repeat center center;
		.icon-guanbi {
			position: absolute;
			cursor: pointer;
			font-size: 24px;
			color: #fff;
			right: 55px;
			top: 55px;
		}
	}
	
	.updateinfo_modal_textbox {
		width: 430px;
		height: 180px;
		box-sizing: border-box;
		padding: 15px; //background: #000;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -48px);
		overflow-y: auto;
		font-size: 16px;
		color: #35383A
	}
	
	.updateinfo_modal_title {
		font-size: 22px;
		color: #35383A;
		position: absolute;
		top: 140px;
		left: 200px;
	}
	
	.updateinfo_modal_time {
		font-size: 14px;
		color: #A0A4AA;
		position: absolute;
		top: 170px;
		left: 200px;
	}
	
	.updateinfo_modal_btn {
		width: 123px;
		height: 40px;
		background: #63D376;
		border-radius: 20px;
		line-height: 40px;
		text-align: center;
		color: #fff;
		font-size: 16px;
		position: absolute;
		left: 50%;
		bottom: 60px;
		cursor: pointer;
		transform: translateX(-50%);
	}
	.homepage_table_content{
		background: #F3F6FB !important;
	}
</style>


